import {
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import './Card.css'
interface State {
  clicked: boolean
  numClicks: number
  id: string
  deleteClicked:boolean
  showClicked: boolean
  isFocused: boolean
}

interface Card {
  name: string;
  total_issues: number;
  id:number;
  created_at:number;
  last_updated:number;
  date: number; // Assuming date is a Unix timestamp
}

interface CardListProps {
  cards: Card[];
}

/**
 * This is a React-based component template. The `render()` function is called
 * automatically when your component should be re-rendered.
 */
class MyComponent extends StreamlitComponentBase<State> {
  public state = { numClicks: 0, isFocused: false,clicked:false ,id:'',deleteClicked:false,showClicked:false}
  groupCardsByTime = (cards:Card[],sortProp:string) => {
    const today = new Date();
    today.setHours(0, 0, 0, 0);

    const yesterday = new Date(today);
    yesterday.setDate(today.getDate() - 1);
    const lastWeek = new Date(today);
    lastWeek.setDate(today.getDate() - 7);
    const lastMonth = new Date(today);
    lastMonth.setMonth(today.getMonth() - 1);
    const lastYear = new Date(today);
    lastYear.setFullYear(today.getFullYear() - 1);

    const groupedCards : { [key: string]: Card[] }= {
      "Today": [],
      "Yesterday": [],
      "Previous 7 days": [],
      "Previous 30 days": [],
      "Long time ago": []
    };

    cards.forEach((card:Card) => {
      let cardDate = new Date();
      if(sortProp == 'last_updated'){
        cardDate = new Date(card["last_updated"]);
      }else{
        cardDate = new Date(card["created_at"]);
      }
    

      if (cardDate >= today) {
        groupedCards.Today.push(card);
      } else if (cardDate >= yesterday) {
        groupedCards.Yesterday.push(card);
      } else if (cardDate >= lastWeek) {
        groupedCards["Previous 7 days"].push(card);
      } else if (cardDate >= lastMonth) {
        groupedCards["Previous 30 days"].push(card);
      } else if (cardDate >= lastYear) {
        groupedCards["Long time ago"].push(card);
      }else {
        groupedCards.LongTimeAgo.push(card);
      }
    });

    return groupedCards;
  }
  public render = (): ReactNode => {
    // Arguments that are passed to the plugin in Python are accessible
    // via `this.props.args`. Here, we access the "name" arg.
    const cards :Card[] = this.props.args["data"]
    const type: string = this.props.args["type"]
    let groupedCards : {[key: string]: Card[]} = {}
    if(type == 'last_updated' || type == 'created_at'){
      groupedCards = this.groupCardsByTime(cards,type);
    }
    
    

 
    // Streamlit sends us a theme object via props that we can use to ensure
    // that our component has visuals that match the active theme in a
    // streamlit app.
    const { theme } = this.props
    const style: React.CSSProperties = {}

    // Maintain compatibility with older versions of Streamlit that don't send
    // a theme object.
    if (theme) {
      // Use the theme object to style our button border. Alternatively, the
      // theme style is defined in CSS vars.
      const borderStyling = `1px solid ${
        this.state.isFocused ? theme.primaryColor : "gray"
      }`
      style.border = borderStyling
      style.outline = borderStyling
    }
    
    // Show a button and some text.
    // When the button is clicked, we'll increment our "numClicks" state
    // variable, and send its new value back to Streamlit, where it'll
    // be available to the Python program.
    return (
      
      <div className="parent-container">
        {type === "last_updated" || type=="created_at"?
        Object.keys(groupedCards).map((timeFrame) => (
          <div key={timeFrame}>
            {groupedCards[timeFrame].length>0?<h6 className="group">{timeFrame}</h6>:<h6></h6>}
            {groupedCards[timeFrame].map((card, index) => (
              <a  target="_self" className="card" key={index} onClick={() => this.onShowClicked(card.id)}>
                <div className="content">
                  <div className="card-name">{card.name}</div>
                  <div className="card-issues">{card.total_issues} issues</div>
                  
                </div>
                <div className="action-buttons">
                  <button className="delete-button" onClick={() => this.onDeleteClicked(card.id)}>
                  <svg stroke="currentColor" fill="none" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="h-4 w-4" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><polyline points="3 6 5 6 21 6"></polyline><path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path><line x1="10" y1="11" x2="10" y2="17"></line><line x1="14" y1="11" x2="14" y2="17"></line></svg>

                  </button>
                </div>
              </a>
            ))}
          </div>
        )):
        cards.map((card, index) => (
          <a  className="card" key={index} onClick={() => this.onShowClicked(card.id)}>
            <div className="content">
              <div className="card-name">{card.name}</div>
              <div className="card-issues">{card.total_issues} issues</div>
              
            </div>
            <div className="action-buttons">
              <button className="delete-button" onClick={() => this.onDeleteClicked(card.id)}>
                <svg stroke="currentColor" fill="none" strokeWidth="2" viewBox="0 0 24 24" strokeLinecap="round" strokeLinejoin="round" className="h-4 w-4" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><polyline points="3 6 5 6 21 6"></polyline><path d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"></path><line x1="10" y1="11" x2="10" y2="17"></line><line x1="14" y1="11" x2="14" y2="17"></line></svg>
              </button>
            </div>
          </a>
        ))
      }

      </div>
    );
  }
  deleteclick = false
  /** Click handler for our "Click Me!" button. */
  private onDeleteClicked = (buttonId: number)=> {
    // Increment state.numClicks, and pass the new value back to
    // Streamlit via `Streamlit.setComponentValue`.
    this.deleteclick = true
    console.log('delete clicked')
    this.setState(
      prevState => ({ deleteClicked: true }),
      () => Streamlit.setComponentValue({'type':'delete','id':buttonId})
    )
    this.setState(
      prevState => ({ deleteClicked: true }),
      () => Streamlit.setComponentValue(false)
    )
    return (event: React.MouseEvent<HTMLButtonElement>) => {
      event.stopPropagation();
      console.log(`Child clicked with index`);
    };
    
    
  }
  private onShowClicked = (buttonId: number): void => {
    
    if(!this.deleteclick){
    console.log('Show clicked')
    // Increment state.numClicks, and pass the new value back to
    // Streamlit via `Streamlit.setComponentValue`.
    this.setState(
      prevState => ({ numClicks: prevState.numClicks + 1,clicked:true }),
      () => Streamlit.setComponentValue({'type':'show','id':buttonId})
    )
    }
    this.deleteclick = false
    
    
  }
  /** Focus handler for our "Click Me!" button. */
  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  /** Blur handler for our "Click Me!" button. */
  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(MyComponent)
